.hero{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    margin: 80px 170px;
}

.hero img{
    margin-top:150px ;
}

.hero h1{
    text-align: center;
    width: 70%;
    font-size: 90px;
    font-weight: 600;
}


.hero h1 span{
    background:linear-gradient(270deg, #DE8908 10%,#B415FF 100%) ;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.hero p{
    width: 50%;
    text-align: center;
    font-size: 24px;
    line-height: 40px;
}

.hero-action{
    display: flex;
    align-items: center;
    gap: 25px;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 50px;
}


.hero-connect{
    padding: 24px, 45px;
    border-radius: 50px;
    background: linear-gradient(267deg,#DA7C25 0.36%,#B923E1 102.06%);
    cursor: pointer;
}

.hero-resume{
    padding: 25px, 75px;
    border-radius: 50px;
    border: 2px solid #FFF;
    cursor: pointer;
}

.hero-connect:hover{
    border: 2px solid #FFF;
}

.hero-resume:hover{
    border-color: #B415FF;
}